<div class="view-anthill"><div class="generators"><generator v-for="(generator, name) in generators" :name="generator.name" :amount="state.generators[name]" :bonus="bonuses.generators[name].value" :cost="multiplierPrices.generators[name].amount.value" :generator-name="name" :multiplier="state.selectedBuyMultipliers.anthill" @buy="$emit('buy', name)"></generator></div><anthill ref="anthill" @load="emitAnthillWidth"></anthill><ants :ants-per-second="antsPerSecond"></ants><button class="navigate upgrades" @click="$emit('navigate', 'upgrades')">Go to upgrades</button><button class="navigate prestige" @click="$emit('navigate', 'prestige')">Go to prestige</button><buy-multiplier-selector v-model="state.selectedBuyMultipliers.anthill"></buy-multiplier-selector></div>