<div class="view-upgrades" :style="{ '--scale': scale }"><h2>Upgrades</h2><div class="upgrades"><upgrade v-for="(definition, id) in upgrades" :name="definition.name" :description="definition.description" :cost="definition.baseCost.amount" :id="id" @buy="$emit('buy', id)"></upgrade></div><button class="navigate anthill" @click="$emit('navigate', 'anthill')">Return to Anthill</button></div>