
import { defineComponent } from "vue"
import { useVModel } from "@vueuse/core"

export default defineComponent({
  props: {
    modelValue: {
      type: Number,
      required: true,
    },
  },
  setup(props, { emit }) {
    const selectedMultiplier = useVModel(props, "modelValue", emit)

    return {
      selectedMultiplier,
      options: {
        "1x": 1,
        "10x": 10,
        "25x": 25,
        "50x": 50,
        "100x": 100,
        Max: Infinity,
      },
    }
  },
})
