<div class="generator"><img class="icon" :src="`generators/${generatorName}.png`"><div class="name">{{ name }}</div><div class="amount">{{ format(amount.bought) }} [+ {{ format(amount.generated) }}]</div><div class="multiplier">x{{ format(bonus) }}</div><button class="buy" @click="$emit('buy')" :class="buttonState">Cost: {{ format(cost) }} (+ {{ multiplierPrices.generators[generatorName].buyAmount.value - amount.bought }})</button></div>