
import { computed, defineComponent } from "vue"
import { format, useProvidedGame } from "@/lib"
import { Currency } from "@/game/currency"

export default defineComponent({
  props: {
    name: {
      type: String,
      required: true,
    },
    amount: {
      type: Object,
      required: true,
    },
    bonus: {
      type: Number,
      required: true,
    },
    cost: {
      type: Number,
      required: true,
    },
    generatorName: {
      type: String,
      required: true,
    },
    multiplier: {
      type: Number,
      required: true,
    },
  },
  emits: ["buy"],
  setup(props) {
    const { state, multiplierPrices } = useProvidedGame()

    const buttonState = computed(() => {
      if (state.currencies[Currency.CRUMBS] >= props.cost) {
        return "available"
      }

      return "unavailable"
    })

    return { format, buttonState, multiplierPrices }
  },
})
